<template>
  <div class="Default">
    <div class="container">
      <div class="row">
        <div id="centerText">
          <span style="font-size: 30px;" id="text">一个</span>
          <span style="font-size: 40px;" id="text">神奇的</span>
          <span style="font-size: 20px;" id="text">网站</span>
        </div>
        <div id="belowText">
          <span style="font-size: 50px" id="bText">你想要的</span>
          <span style="font-size: 40px" id="bText">兔子都有</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Default',
  created() {
    
  }
}
</script>

<style>
#centerText {
  text-align: center;
  margin: 20% 20% 0% 20%; 
  color: #d87676;
}

#belowText {
  text-align: center;
  color: #d87676;
}

@keyframes textDrop {
  from {
    bottom: 100px;
  }to{
    bottom: 0px;
  }
}
@keyframes textUp {
  from {
    top: 100px;
  }to{
    top: 0px;
  }
}



#text{
  position: relative;

  /* 执行过渡的属性 */
  transition-property: color;
  /* 过渡的时间 */
  transition-duration: 1s;
  /* 设置过渡时的变化方式 */
  transition-timing-function: ease;
  /* 延时 */
  transition-delay: 0;

  animation: textDrop 2s;
}
#text:hover {
    color: #FFFFFF;
}

#bText {
  position: relative;

  /* 执行过渡的属性 */
  transition-property: color;
  /* 过渡的时间 */
  transition-duration: 1s;
  /* 设置过渡时的变化方式 */
  transition-timing-function: ease;
  /* 延时 */
  transition-delay: 0;

  animation: textUp 2s;
}
#bText:hover {
  color: #FFFFFF;
}
</style>